<template>
<main>
  <div :key="item.name" v-for="item in items">
  <router-link :to="{ name: 'initialUrl', params: { productId: item.id } }">
    <section>
      <h4> {{item.name}} </h4>
    </section>
    <img draggable="false" :src="item.url" alt="No Image" />
  </router-link>
  </div>
</main>
</template>
<script>
import Data from '@/mixins/getItems.js'
export default {
  name: 'home',
  data () {
    return {
      items: []
    }
  },
  mixins: [Data]
}
</script>
<style scoped>
    main {
        display: flex;
        justify-content: space-around;
        flex-flow: row wrap;
    }
    main>div {
        background-color: white;
        height: 200px;
        width: 80%;
        overflow: hidden;
        margin: 2vh;
        border: 3px black solid;
        border-top-right-radius: 20%;
    }
    img {
        height: 100%;
        width: 100%;
    }
    section {
      position: absolute;
    }
    @media screen and (min-width: 720px) {
        main>div {
            margin: 30px;
            height: 400px;
            width: 400px;
            transition-duration: .2s;
        }
        main>div>section {
            display: none ;
        }
        main>div:hover {
            transform: scale(0.94);
        }
    }
</style>
