<template>
  <main class="order">
    <form method="post" @submit.prevent="itemsUpdate">
      <table>
        <thead>
          <tr>
            <th>Item Name</th>
            <th>Item In Stock</th>
            <th>Quantity (in Carton)</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in items" :key="index">
            <td style="text-align:left;"> {{ item.name }} </td>
            <td style="text-align:center;"> {{ item.instock }} </td>
            <td style="text-align:center;"> {{ item.quantity }} </td>
            <td><button :class="{disabled: item.quantity>=100 || item.quantity>=item.inStock}" type="button" :disabled="item.quantity>100 || item.quantity>=item.inStock" @click="item.quantity++">+</button><button :class="{disabled: item.quantity<=0}" type="button" :disabled="item.quantity<=0" @click="item.quantity--">-</button></td>
          </tr>
          <tr>
            <td></td>
            <td style="text-align:right;"> Total :</td>
            <td merge>{{ total }}</td>
            <td><input type="submit" class="submit" value="SUBMIT" :class="{disabled: total<1}" :disabled="total<1"></td>
          </tr>
          <tr>
            <input type="radio" name="orderType" v-model="orderType" value="State">State
            <input type="radio" name="orderType" v-model="orderType" value="Inter-State">Inter-State
          </tr>
        </tbody>
        <select name="orderOpt" v-model="orderOpt" placeholder="Select Any Option">
          <option value="Deliver">Deliver It</option>
          <option value="Pickup">Pickup By Myself</option>
        </select>
      </table>
    </form>
  </main>
</template>

<script>
export default {
  name: 'order',
  data () {
    return {
      orderOpt: 'Deliver',
      orderType: 'State',
      items: [],
      item: null
    }
  },
  created () {
    this.$http.get('https://api.jsonbin.io/b/5eef8dc8e2ce6e3b2c76de3c').then(response => { // https://api.jsonbin.io/b/5eef8dc8e2ce6e3b2c76de3c
      this.items = response.body
    }, response => {
      setTimeout(function () {
        alert('The Server is not Running')
      }, 10000)
    })
  },
  computed: {
    total () {
      let sum = 0
      for (let i = 0; i < this.items.length; i++) {
        sum += parseInt(this.items[i].quantity)
      }
      return sum
    }
  },
  methods: {
    itemsUpdate (value) {
      this.items.forEach((item, i) => {
        console.log(value)
        item.inStock -= item.quantity
        item.quantity = 0
      })
      this.post(this.items)
    },
    post (Data) {
      this.$http.post('localhost:3000/items', Data)
    }
  },
  watch: {
  }
}
</script>
<style scoped>
table {
  font-size: 8px;
  position: relative;
  margin: auto;
  font-family: Helvetica;
  text-align: center;
  background: rgb(122, 128, 252);
}
th {
  background: rgb(122, 128, 252);
  height: 3vh;
}
tr {
  background: rgb(170, 173, 244);
}
td {
  padding: 0.1vh;
  width: 10vw;
}
button {
  background-color: #4b4453;
  border: none;
  text-align: center;
}
.disabled {
  opacity: 0.1;
}
.submit {
  width: 70px;
  height: 20px;
  background-color: #4b4453;
  border-radius: 35px;
  box-shadow: 8px 8px 14px 1px rgba(00,00,00,0.1);
  border: none;
  text-align: center;
  cursor: pointer;
  padding: auto;
}
@media screen and (min-width: 720px) {
  table {
    font-size: 20px;
  }
  button {
    cursor: pointer;
    font-size: 20px;
    height: 20px;
    overflow: hidden;
    width: 40px;
    margin: auto;
    transition: all .5s;
  }
  button:active {
    box-shadow: none;
  }
  .submit {
    width: 110px;
    height: 30px;
  }
}
</style>
